import * as React from 'react'
import { Link, type HeadFC, type PageProps } from 'gatsby';

import { Button, Container, Image } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEtsy, faFacebookF, faInstagramSquare, faPinterestP } from '@fortawesome/free-brands-svg-icons';

import Navigation from '../components/navigation';
import Footer from '../components/footer';

import resin1Image from '../images/resin-1.png';
import resionArtworkImg from '../images/resin-artwork.png';
import resionBookmarkImg from '../images/resin-bookmark.png';
import resionCoastersImg from '../images/resin-coasters.png';
import resinShowcaseImage from '../images/resin-showcase.png';
import resionVanityImg from '../images/resin-vanity.png';

import './resin.css';
import LeftLargeImageCard from '../components/left-large-image-card';


const ResinPage: React.FC<PageProps> = () => {
  return (
    <>
      <Navigation />
      <main>
        <div className='d-flex py-5'>
          <Container className='flex-fill d-flex my-story-container'>
            <div className='my-story flex-fill'>
              <div className='row'>
                <div className='col-12 col-lg-7'>
                  <div className='d-flex flex-fill justify-content-center p-5'>
                    <div className='resin-showcase p-5'>
                      <Image className='showcase-image' src={resinShowcaseImage} fluid />

                      <div className="text-center p-2">
                        <Link className='col-2 p-2' to='https://www.facebook.com/love2craftllc.2024/'>
                          <FontAwesomeIcon icon={faFacebookF} color="black" />
                        </Link>
                        <Link className='col-2 p-2' to='https://www.pinterest.com/love2craftllc/'>
                          <FontAwesomeIcon icon={faPinterestP} color="black" />
                        </Link>
                        <Link className='col-2 p-2' to='https://www.instagram.com/love2craftllc/'>
                          <FontAwesomeIcon icon={faInstagramSquare} color="black" />
                        </Link>
                        <Link className='col-2 p-2' to='https://www.etsy.com/shop/LOVE2CRAFTLLC'>
                          <FontAwesomeIcon icon={faEtsy} color="black" />
                        </Link>
                      </div>
                    </div>
                  </div>
                </div>
                <div className='col-12 col-lg-5 p-5'>

                  <h1>Resin art</h1>
                  <div>
                    <p className='p-2'>Each resin piece is hand-poured with care, creating beautiful swirls and colors that can never be replicated.</p>
                    <p className='p-2'>From coasters to artwork, every design reflects creativity and craftsmanship — no two are ever the same.</p>
                    <div className='p-2 d-flex justify-content-between'>
                      <div className='flex-grow-1'>
                        <p>Explore more one-of-a-kind resin pieces and new designs in our Etsy shop.</p>
                      </div>
                      <Link className='p-2' to='https://www.etsy.com/shop/LOVE2CRAFTLLC'>
                        <Button variant="outline-dark text-nowrap">Our Store</Button>
                      </Link>
                    </div>
                  </div>

                  <Image className='py-5' src={resin1Image} fluid />
                </div>
              </div>
            </div>
          </Container>
        </div >
        <div className="d-flex justify-content-start flex-wrap px-5">
          <div className='p-4 align-items-stretch col-12 col-xl-6 col-xxl-4'>
            <LeftLargeImageCard
              link="https://www.etsy.com/shop/LOVE2CRAFTLLC?section_id=48503419"
              linkText="Etsy link"
              image={resionCoastersImg}
              title="Resin Coasters for Drinkware"
              description={
                <div>
                  <p>Each resin piece is hand-poured with care, creating stunning patterns and colors that can never be repeated.</p>
                  <p>From coasters to artwork, every design is one-of-a-kind and reflects the creativity and heart behind Love2CraftLLC.</p>
                </div>
              }>
            </LeftLargeImageCard>
          </div>
          <div className='p-4 align-items-stretch col-12 col-xl-6 col-xxl-4'>
            <LeftLargeImageCard
              link="https://www.etsy.com/shop/LOVE2CRAFTLLC?section_id=50526514"
              linkText="Etsy link"
              image={resionVanityImg}
              title="Resin Vanity Trays"
              description={
                <div>
                  <p>Perfect for holding jewelry, candles, or makeup, each resin vanity tray is crafted to bring beauty and function to your space.</p>
                  <p>Every tray features one-of-a-kind colors and finishes</p>
                </div>
              }>
            </LeftLargeImageCard>
          </div>
          <div className='p-4 align-items-stretch col-12 col-xl-6 col-xxl-4'>
            <LeftLargeImageCard
              link="https://www.etsy.com/shop/LOVE2CRAFTLLC?section_id=50526538"
              linkText="Etsy link"
              image={resionBookmarkImg}
              title="Resin Bookmarks"
              description={
                <div>
                  <p>Each resin bookmark is a little work of art — lightweight, glossy, and completely unique.</p>
                  <p>They make perfect gifts for book lovers who appreciate creativity in every page turn.</p>
                </div>
              }>
            </LeftLargeImageCard>
          </div>
          <div className='p-4 align-items-stretch col-12 col-xl-6 col-xxl-4'>
            <LeftLargeImageCard
              link="https://www.etsy.com/shop/LOVE2CRAFTLLC?section_id=50526678"
              linkText="Etsy link"
              image={resionCoastersImg}
              title="Resin Keychains"
              description={
                <div>
                  <p>Our handmade resin keychains are full of color and character!</p>
                  <p>Whether for your keys, bags, or gifts, each one is crafted with playful designs and a personal touch.</p>
                </div>
              }>
            </LeftLargeImageCard>
          </div>
          <div className='p-4 align-items-stretch col-12 col-xl-6 col-xxl-4'>
            <LeftLargeImageCard
              link="https://www.etsy.com/shop/LOVE2CRAFTLLC?section_id=50554412"
              linkText="Etsy link"
              image={resionArtworkImg}
              title="Resin Artwork"
              description={
                <div>
                  <p>Our handmade resin keychains are full of color and character!</p>
                  <p>Whether for your keys, bags, or gifts, each one is crafted with playful designs and a personal touch.</p>
                </div>
              }>
            </LeftLargeImageCard>
          </div>
        </div>
      </main >
      <div className="text-black container-fluid" style={{ backgroundColor: 'rgb(244, 235, 215)' }}>
        <div className="p-2 d-flex flex-fill justify-content-center">
          <h1>View on Etsy</h1>
        </div>
        <div className="p-2 d-flex flex-fill justify-content-center">
          Discover new seasonal designs and custom items available exclusively on Etsy.
        </div>
        <div className="p-2 d-flex flex-fill justify-content-center">
          Shop Greeting Cards on Etsy →
        </div>
        <div className="p-2 d-flex flex-fill justify-content-center">
          <Link className='p-2' to='https://www.etsy.com/shop/LOVE2CRAFTLLC'>
            <Button variant="outline-dark text-nowrap">Our Store</Button>
          </Link>
        </div>
      </div>
      <Footer />
    </>
  )
}

export default ResinPage

export const Head: HeadFC = () => <title>Resin art</title>