import React from 'react';
import { Button } from 'react-bootstrap';
import Card from 'react-bootstrap/Card';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';

import './left-large-image-card.css';
import { Link } from 'gatsby';

export interface LeftLargeImageCardProps {
  image: any;
  title: string;
  description: any;
  link: string;
  linkText: string;
}

function LeftLargeImageCard({ image, title, description, link, linkText }: LeftLargeImageCardProps) {
  return (
    <Card className='h-100 left-large-card'>
      <Row className="g-0"> {/* Use g-0 to remove gutter between columns */}
        <Col md={7}> {/* Adjust column size for image as needed */}
          <Card.Img className='img-fluid img-thumbnail' src={image} />
        </Col>
        <Col md={5}> {/* Adjust column size for card body as needed */}
          <Card.Body>
            <Card.Title className='left-large-card-title'>{title}</Card.Title>
            <Card.Text className='left-large-card-text'>
              {description}
            </Card.Text>
            <div className='d-flex justify-content-end pt-2'>
              <Link className='' to={link}>
                <Button variant="light">{linkText}</Button>
              </Link>
            </div>
          </Card.Body>
        </Col>
      </Row>
    </Card>
  );
}

export default LeftLargeImageCard;